@use '../base/variables' as v;
@use '../base/mixins' as mx;
@use "sass:color";

.alerta{
    padding: .5rem;
    text-align: center;
    color: v.$blanco;
    font-weight: v.$bold;
    text-transform: uppercase;
    margin: 1rem 0;
    &.error{
        background-color: v.$rojo;
    }
    &.exito{
        background-color: v.$verde;
    }
}

table.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: v.$verde;
        th{
            color: v.$blanco;
            padding: 2rem;
        }
    }
    .imagen_tabla{
        width: 10rem;
    }
}
