@use '../base/variables' as v;
@use '../base/mixins' as mx;
@use "sass:color";

.no-webp .inicio{
    background-image: url(../img/header.jpg);

}
.webp .inicio{
    background-image: url(../img/header.webp);
}


.header{
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center, center;
        background-size: cover;
        @include mx.tablet{
            min-height: 70rem;
        }

        .contenido_header{
            @include mx.tablet{
                min-height: 70rem;
            }
        }
    }
}

.contenido_header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1{
        text-align: center;
        color: v.$blanco;
        font-weight: v.$bold;
        line-height: 2;
        max-width: 60rem;

        @include mx.tablet{
            text-align: left;
        }
    }
}
